<template>
    <div>
        <router-link class="logo" to="/">
            <span class="iconfont icon-custom-logo"></span>
            <span class="top">音乐Top</span>
        </router-link>
        <hr class="line">
        <div class="album_about">
            <div class="album_left">
                <img class="album_img" :src="album.picUrl">
                <el-button  @click="drawer = true" type="primary" style="margin-left: 16px;">
                专辑简介
                </el-button>

                <el-drawer
                title="我是标题"
                v-model="drawer"
                :direction="direction"
                :with-header="false">
                <div class="intro">{{album.description}}</div>
                </el-drawer>
            </div>
            <div class="album_right">
                <h3 class="right_h3"><span>{{album.type}}:</span>
                    <span>{{album.name}}</span>
                </h3>
                <p class="right_name">
                    <span>歌手：</span>
                    <span v-for="(item,index) in album.artists" :key="index">{{item.name}} / </span>
                </p>
                <p class="company">
                    <span>发行公司：{{album.company}}</span>
                </p>

                <h4 class="songs_h3">歌曲列表</h4>
                <div class="songs">
                    <div class="songhd">
                        <span>歌曲</span>
                        <span>演唱</span>
                        <span>播放</span>
                    </div>
                    <div class="songsing" v-for="(item,index) in songs" :key="index">
                        <span>{{item.name}}</span>
                        <span>{{item.ar[0].name}}</span>
                        <router-link class="singa" :to="{path:'/player',query:{id:item.id}}">
                            <span class="iconfont icon-bofang1"></span>
                        </router-link>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            album:{},
            songs:[],
            drawer: false,
            direction: 'ltr'
        }
    },
    methods: {
        useraxios(httpUrl){
            return new Promise((resolve,reject)=>{
                 axios.post(httpUrl).then((res)=>{
                    resolve(res.data)
                })
                .catch((res)=>{
                    console.log(res)
                })
            })
            
        },
    },
    mounted() {
        this.useraxios(`http://musicapi.leanapp.cn/album?id=${this.$route.query.id}`)
        .then((data)=>{ 
            this.album=data.album,
            this.songs=data.songs        
        });
    },
}
</script>

<style lang="less" scoped>
@mainColor:#00B9F2;
.logo{
    display: block;
    text-decoration: none;
    color: @mainColor;
    margin: 10px 0px 10px 50px;
    .iconfont{
        font-size: 30px;
    }
    .top{
        font-size: 24px;
        font-weight: 700;
    }
}
.line{
    width: 100%;
    border:1px solid @mainColor;
}
.album_about{
    width: 80%;
    min-width: 900px;
    margin: 50px auto;
    display: flex;
}
.album_left{
    width: 300px;
}
.album_right{
    margin-left: 30px;
}
.album_img{
        display: block;
        width: 250px;
        height: 250px;
        margin-bottom: 30px;
}
.intro{
    font-size: 14px;
    letter-spacing: 2px;
    text-indent: 28px;
    color: #999;
    height: 100vh;
    overflow: auto;
    padding-left: 20px;
}
.right_h3{
    font-size: 28px;
    line-height: 37px;
    letter-spacing: 4px;
}
.right_h3 span:nth-child(1){
    display: inline-block;
    font-size: 24px;
    color: #666;
    font-weight: 400;
    margin-bottom: 30px;
}
.right_name span:first-child {
    color: #666;
}
.right_name span{
    color: @mainColor;
}
.company{
    margin-top: 50px;
    font-size: 14px;
    color: #666;
}
.songs_h3{
    margin: 50px 0px 20px 0px;
    color: #999;
}
.songhd{
    width: 700px;
    height: 30px;
    margin: 0px auto;
    line-height: 30px;
    border-top: 1px solid #E6E6E6;
    border-bottom: 1px solid #E6E6E6;
    background: #F8F9F9;
    display: flex;
    color: @mainColor;
    font-weight: 900;
    font-size: 18px;
    span{
        display: block;
        width: 25%;
        text-align: left;
        font-family: "楷体";
    }
    span:nth-child(1){
        padding-left: 5%;
    }
    span:nth-child(2){
        width: 40%;
        text-align: center;
    }
    span:nth-child(3){
        text-align: right;
    }
}
.songsing{
    .songhd;
    border:none;
    font-weight: normal;
    color: #666;
    cursor: pointer;
    .iconfont{
        display: inline;
        padding-right: 10px;
        color: @mainColor;
    }
}
.songsing span:first-child{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.songsing:nth-child(even){
    background: #FFF;
}
.songsing:hover{
    background: rgba(0,185,242, 0.2);
}
.songlast{
    margin-bottom: 100px;
}
.singa{
    display: block;
    width: 25%;
    text-decoration:none;
    text-align: right;
}
</style>
